@mixin breakPoints($point){
    @if $point == xxl {
        @media (max-width:2000px){
            @content;
        }
    }
    @else if $point == xl {
        @media (max-width:1600px){
            @content;
        }
    }
    @else if $point == lg {
        @media (max-width:992px){
            @content;
        }
    }
    @else if $point == md {
        @media (max-width:768px){
            @content;
        }
    }
    @else if $point == sm {
        @media (max-width:576px){
            @content;
        }
    }
    @else if $point == xs {
        @media (max-width:320px){
            @content;
        }
    }
}